//This component is for displaying both success and failure messages

'use client'
import '../../public/css/bootstrap.css';
import Card from 'react-bootstrap/Card';

function Feedback(p) {

    const {color,code,title,text,hidden=true} = p;

  return (
    <Card bg={color} hidden={hidden}>
        <Card.Header className='text-dark'>{code}</Card.Header>
        <Card.Body>
            <Card.Title className='text-dark'>{title}</Card.Title>
            <Card.Text className='text-dark'>{text}</Card.Text>
        </Card.Body>
    </Card>
  );
}

export default Feedback;