'use client'
import '../../public/css/bootstrap.css';
import Form from 'react-bootstrap/Form';


export default function CheckBox(p){

    const {label, reminder, isChecked,dispatch,hidden=false} = p;

    return (
        <Form.Group hidden={hidden} className="mb-3 text-dark-emphasis" controlId={`form${label}`}>
            <Form.Check type="checkbox" label={reminder} checked={isChecked} onChange={e=>dispatch({type:'mark-Checked',label:label, value:!isChecked})} />
        </Form.Group>
    )

}
