'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import MultiSelect from "@/app/_components/multiselect";
import FormDescription from "@/app/_components/formdescription";

let submitObject = {};

let pageDescription = ['Create a new LCS Classroom session in Rent Manager','and enroll learners ready for the course to the session.']

const initState = {
    'Select Course':{
        options: [{ID:0, Name:''}],
        value:0
    },
    'Trainer':{
        options: [{ID:0,Name:''}],
        value:0
    },
    'Remote Trainer':{
        options: [],
        value:0
    },
    'Room':{
        options: [],
        value:236
    },
    'Shadow Name': {
        value: '',
    },
    'Teams Link': {
        value: '',
    },
    'Session Date': {
        value: ''
    },
    'Session Length': {
        value: 60
    },
    'Session Note': {
        value: ''
    },
    'Select Students':{
        options: [],
        value:[]
    },
    'Has Shadow':{
        isChecked: false
    },
    'Needs Reserved':{
        isChecked: false
    },
    'Has Remote':{
        isChecked: false
    },
    'Assets List':[],
    'Sessions List':[],
    'Trainers List':[],
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};

const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            console.dir(action);

            newState[action.label].value = action.value;

            if(action.label=='Select Course'){
                
                let course = action.value;

                try{

                    newState['Select Students'].options = state['Sessions List'].find(session=>session.ID == course).Learners;

                }catch{
                    newState['Select Students'].options = [];
                }

                

            }

            return newState

            break
        }

        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }

        case 'load-Options':{

            let Assets = action.data.Assets;

            let Sessions = action.data.Sessions;

            let Trainers = action.data.Trainers;

            //Add unassigned. This needs to be updated because it breaks
            //the rules of react useEffect (but still works)

            Trainers.unshift({ID:0,Name:'UnAssigned'});

            let Rooms = action.data.Rooms;

            newState['Assets List'] = Assets;

            newState['Sessions List'] = Sessions;

            newState['Trainers List'] = Trainers;

            newState['Select Course'].options = Assets;

            newState['Select Course'].value = Assets[0].ID;
            
            newState['Room'].options = Rooms;

            newState['Room'].value = Rooms[0].ID;

            newState['Trainer'].options = Trainers;

            newState['Trainer'].value = Trainers[0].ID;

            newState['Remote Trainer'].options = Trainers;

            newState['Remote Trainer'].value = Trainers[0].ID;

            //This should make it that the first option (Train the Trainer At Time of writing) Autoloads learners

            try{

                newState['Select Students'].options = newState['Sessions List'].find(session=>session.ID == newState['Select Course'].value).Learners;

            }catch{

                newState['Select Students'].options = [];
            }


            //newState['Select Students'].options = Sessions.find(session=>session.ID == Assets[0].ID).Learners;


            return newState

            break;
        }

        case 'load-Response':{

        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function CreateSession() {

    const [state, dispatch] = useReducer(reducer,initState);



    useEffect(function(){

                const loadDropListOptions = async()=>{

                    let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveLiveTrainingOptions'});

                    if(options.didError){

                        //Do stuff

                        return

                    }

                    dispatch({type:'load-Options',data:options.data})

                }

                loadDropListOptions();
            
                return

            },[]
    );

    submitObject = {
        Course: state['Select Course'].value,
        Students:state['Select Students'].value,
        SessionDateTime:state['Session Date'].value,
        SessionLength:state['Session Length'].value,
        Trainer:state['Trainer'].value,
        IsShadow:state['Has Shadow'].isChecked,
        Shadow:state['Shadow Name'].value,
        Room:state['Room'].value,
        NeedsReserved:state['Needs Reserved'].isChecked,
        TeamsLink:state['Teams Link'].value,
        RemoteTrainerName:state['Has Remote'].isChecked ? state['Trainer'].options.find(tr=>{return tr.ID == state['Remote Trainer'].value}).Name : '',
        AssetName: state['Select Course'].options.find(asset=>{return asset.ID == state['Select Course'].value}).Name,
        AssetID: state['Select Course'].options.find(asset=>{return asset.ID == state['Select Course'].value}).AssetID,
        SessionNote:state['Session Note'].value
    };

  return (
    <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
      <Form>
        <FormTitle text='Create Session'></FormTitle>
        <FormDescription text={pageDescription}></FormDescription>
        <Select
            options={state['Select Course'].options}
            label='Select Course'
            value={state['Select Course'].value}
            dispatch={dispatch}
        ></Select>
        <MultiSelect
            options={state['Select Students'].options}
            label='Select Students'
            value={state['Select Students'].value}
            dispatch={dispatch}
        >
        </MultiSelect>
        <EntryBox
            label='Session Date'
            type = 'datetime-local'
            reminder =''
            placeholder = ''
            value = {state['Session Date'].value}
            dispatch={dispatch}
        ></EntryBox>
        <EntryBox
            label='Session Length'
            type = 'numeric'
            reminder =''
            placeholder = ''
            value = {state['Session Length'].value}
            dispatch={dispatch}
        ></EntryBox>

        <Select
            options={state['Trainer'].options}
            label='Trainer'
            value={state['Trainer'].value}
            dispatch={dispatch}
        ></Select>
        <CheckBox
            label="Has Shadow"
            reminder='Check to indicate a Shadow'
            isChecked={state['Has Shadow'].isChecked}
            dispatch={dispatch}
            hidden={false}
        ></CheckBox>
        <EntryBox
            label='Shadow Name'
            type = 'text'
            reminder =''
            placeholder = ''
            value = {state['Shadow Name'].value}
            dispatch={dispatch}
            hidden={!state['Has Shadow'].isChecked}
        ></EntryBox>
        
        <CheckBox
            label="Has Remote"
            reminder='Check to indicate a Remote Trainer'
            isChecked={state['Has Remote'].isChecked}
            dispatch={dispatch}
            hidden={false}
        ></CheckBox>
        <Select
            options={state['Remote Trainer'].options}
            label='Remote Trainer'
            value={state['Remote Trainer'].value}
            dispatch={dispatch}
            hidden={!state['Has Remote'].isChecked}
        ></Select>
        
        <Select
            options={state['Room'].options}
            label='Room'
            value={state['Room'].value}
            dispatch={dispatch}
        ></Select>
        <CheckBox
            label="Needs Reserved"
            reminder='Check to indicate a needs reserved'
            isChecked={state['Needs Reserved'].isChecked}
            dispatch={dispatch}
            hidden={false}
        ></CheckBox>
        <EntryBox
            label='Teams Link'
            type = 'text'
            reminder =''
            placeholder = ''
            value = {state['Teams Link'].value}
            dispatch={dispatch}
        ></EntryBox>
        <EntryBox
            label='Session Note'
            type = 'text'
            reminder =''
            placeholder = ''
            value = {state['Session Note'].value}
            dispatch={dispatch}
        ></EntryBox>
        <Submit 
            method="POST"
            endpoint="Litmos/CreateLCSClass"
            dispatch={dispatch}
            data={submitObject}
            color={state.Submit.Color}
            text={state.Submit.Text}
        ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
      </Form>
    </main>
  );
}
