import axios from "axios";

export async function fetch(p){

    const {method, endpoint, data} = p;

    let axiosOptions = {
        method: method,
        url:`${process.env.NEXT_PUBLIC_SERVER_URL}${endpoint}`,
        data: data !== undefined ? data: null
    };

    let axiosResponse = {};

    axiosResponse.didError = false;

    console.dir(axiosOptions);

    try{

        let axiosAttempt = await axios(axiosOptions);

        axiosResponse.data = axiosAttempt.data;

        axiosResponse.didError = axiosResponse.data.didError;


    }catch{

        axiosResponse.didError = true;

        axiosResponse.data = {}

    }

    return axiosResponse

}