'use client';
import Form from 'react-bootstrap/Form';
import '../../public/css/bootstrap.css';

export default function Select(p) {

  const {options,label,value,dispatch,hidden=false} = p;

  return (
    <Form.Group hidden={hidden}>
      <Form.Label className='text-dark-emphasis badge'>{label}</Form.Label>
      <Form.Select className='mb-3' aria-label={label} value={value} onChange={e=>dispatch({type:'update-Value',value:e.target.value, label:label})}>
        {options.map(o=>{return <option key={o.ID} value={o.ID}>{o.Name}</option>})}
      </Form.Select>
    </Form.Group>
  );
};