'use client'
import '../../public/css/bootstrap.css';
import Form from 'react-bootstrap/Form';

function EntryBox(p) {

    const {label,type='email',reminder, placeholder, value,dispatch,hidden=false} = p;

  return (
      <Form.Group hidden={hidden} className="mb-3" controlId={`form${label}`}>
        <Form.Label className='text-dark-emphasis badge'>{label}</Form.Label>
          <Form.Control type={type} placeholder={placeholder} value={value} onChange={e=>dispatch({type:'update-Value',value:e.target.value, label:label})} />
        <Form.Text className="text-danger badge">
          {reminder}
        </Form.Text>
      </Form.Group>
  );
}

export default EntryBox;