'use client';
import Form from 'react-bootstrap/Form';
import '../../public/css/bootstrap.css';


export default function MultiSelect(p) {

  const {options,label,value,dispatch} = p;

  let argh = options.map(o=>{return<option key={o.ID} value={o.ID}>{o.Name}</option>})

  return (
    <Form.Group>
      <Form.Label className='text-dark-emphasis badge'>{label}</Form.Label>
      <Form.Select className='mb-3'
          aria-label={label} 
          multiple={true} 
          value={value} 
          size={'md'} 
          onChange={e=>dispatch({type:'update-Value',value:[...e.target.selectedOptions].map(o=>o.value), label:label})}>
        {argh.map(e=>{return e})}
      </Form.Select>
    </Form.Group>
  );
};